import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { TextField, ActionField, TextViewUrlField, DateField, StatusField, IndexField } from 'src/app/common-module/table-list/list-field-type';
import { PackingService } from 'src/app/service/packing.service';

@Component({
  selector: 'app-packing',
  templateUrl: './packing.component.html'
})
export class PackingComponent implements OnInit {

  loadingState = true;
  objectArray: Array<any> = [];

  headerData: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  status_id: any;
  statusListArray: any = [];
  constructor(
    private paginationService: PaginationService,
    private packingService: PackingService,
  ) { }

  ngOnInit() {
    this.getMasterData();
    this.setHeaderData();
    this.getObjects();
  }
  getMasterData() {
    this.packingService.getListFilterData().subscribe((responce) => {
      if (responce.success) {
        this.statusListArray = responce.data.status;
      }
    });
  }
  setHeaderData() {
    this.headerData.push(
      new IndexField({
        label: "No.",
      }),
      new DateField({
        label: "Order Date",
        key: "sales_order_date",
      }),
      // new TextViewUrlField({
      //   label: "Pick No",
      //   key: "pick_no",
      //   viewUrl: {
      //     path: '/outbound/pick-list-new/view/',
      //     id: 'sales_order_id'
      //   }
      // }),
      new TextField({
        label: "Product Code",
        key: 'sales_order_no',
      }),

      new TextField({
        label: "Product Name",
        key: "client.label",
      }),
      new DateField({
        label: "Delivery Date",
        key: "po_date",
      }),
      new StatusField({
        label: "Status",
        key: "status.label",
      }),
    );
    let canEdit = true;
    let canView = true;
    let canDelete = true;
    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/outbound/pick-list-new/edit/',
        id: 'sales_order_id'
      }
    }
    if (canView) {
      actions['view'] = {
        path: '/outbound/pick-list-new/view/',
        id: 'sales_order_id'
      }
    }
    if (canDelete) {
      actions['delete'] = {
        path: 'picking/sales-order-list/',
        id: 'sales_order_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }


  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.status_id) {
      params.status_id = this.status_id;
    }

    if (this.searchText) {
      params.search = this.searchText;
    }
    this.packingService.getPackingList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success && response.data) {
        this.objectArray = response.data.list;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    // this.currentPage = 1;
    this.getObjects();
  }

  onReloadEvent() {
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }

}






