import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
@Injectable({
    providedIn: 'root'
})

export class PackingService {

    API_URL: string = environment.APIEndpoint;

    constructor(
        private http: HttpClient,
    ) { }

    //list page
    getListFilterData(param = null): Observable<any> {
        return this.http.get(`${this.API_URL}/picking/list-filter-drp`, { params: param });
    }

    getPackingList(param): Observable<any> {
        return this.http.get(`${this.API_URL}/picking/sales-order-list`, { params: param });
    }

    // add page
    getMasterData(param: any): Observable<any> {
        return this.http.get(`${this.API_URL}/packing/master-data`, { params: param });
    }
    getSalesOrderDetail(id): Observable<any> {
        return this.http.get(`${this.API_URL}/packing/get-product-for-packing/${id}`);
    }



    
    addPacking(data): Observable<any> {
        return this.http.post(`${this.API_URL}/picking/generate`, data);
    }

    editPacking(id, data): Observable<any> {
        return this.http.put(`${this.API_URL}/Packing/${id}`, data);
    }

    getPackingById(id, param = null): Observable<any> {
        return this.http.get(`${this.API_URL}/picking/sales-order-view-pick-list/${id}`, { params: param });
    }
    // getPackingviewProductById(id, param = null): Observable<any> {
    //     return this.http.get(`${this.API_URL}/picking/sales-order-pick-view-product-list/${id}`, { params: param });
    // }

    // packing/get-product-for-packing/
}

